.ant-pagination {
  padding: 0.4rem 0.3rem;
  font-size: 13px;
  color: #9696A3;
  background: #ffffff;
}
.ant-pagination li,
.ant-pagination .ant-pagination-prev .ant-pagination-item-link,
.ant-pagination .ant-pagination-next .ant-pagination-item-link,
.ant-pagination .ant-select:not(.ant-select-customize-input) .ant-select-selector {
  border-radius: 4px;
}
.ant-pagination .ant-pagination-total-text,
.ant-pagination .ant-pagination-options {
  float: right;
}
.ant-pagination a {
  color: #9696A3;
}
.ant-pagination .ant-pagination-options {
  margin-right: 20px;
}
.ant-pagination .ant-pagination-item-active {
  background: #5C5EE4;
  border-color: #7B7DE9;
}
.ant-pagination .ant-pagination-item-active a {
  color: #fff;
}
.ant-pagination .ant-pagination-item:hover {
  background: #7B7DE9;
  border-color: #7B7DE9;
}
.ant-pagination .ant-pagination-item:hover a {
  color: #fff;
}
.ant-pagination .anticon,
.ant-pagination .ant-pagination-prev,
.ant-pagination .ant-pagination-next,
.ant-pagination .ant-pagination-jump-prev,
.ant-pagination .ant-pagination-jump-next {
  color: #9696A3;
}
.ant-pagination .ant-select:not(.ant-select-disabled):hover .ant-select-selector {
  border-color: #7B7DE9;
}
.ant-pagination .ant-pagination-prev:hover .ant-pagination-item-link,
.ant-pagination .ant-pagination-next:hover .ant-pagination-item-link {
  color: #fff;
  border-color: #7B7DE9;
  background: #7B7DE9;
}
.ant-pagination .ant-pagination-prev:hover .anticon,
.ant-pagination .ant-pagination-next:hover .anticon {
  color: #fff;
}
.ant-pagination .ant-pagination-jump-prev .ant-pagination-item-container .ant-pagination-item-link-icon,
.ant-pagination .ant-pagination-jump-next .ant-pagination-item-container .ant-pagination-item-link-icon {
  color: #7B7DE9;
}
@media screen and (max-width: 768px) {
  .ant-pagination {
    margin: 0.4rem 0.3rem;
    font-size: 0.2rem;
    text-align: center;
  }
  .ant-pagination .ant-pagination-total-text,
  .ant-pagination .ant-pagination-item,
  .ant-pagination .ant-pagination-prev,
  .ant-pagination .ant-pagination-next,
  .ant-pagination .ant-pagination-jump-prev,
  .ant-pagination .ant-pagination-jump-next {
    height: 0.54rem;
    min-width: 0.54rem;
    line-height: 0.54rem;
  }
  .ant-pagination .ant-pagination-total-text {
    display: none;
  }
}
