.order {
    font-family: Source Han Sans CN;
    font-weight: 400;
    color: #040E34;

    .card-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 0.26rem;

        p {
            height: 0.18rem;
            font-size: 0.18rem;
            font-family: Source Han Sans CN;
            font-weight: 400;
            line-height: 0.18rem;
            color: #040E34;
            margin-left: 0.3rem;
        }

        .top-search-img {
            width: 0.16rem;
        }

        .top-search-input {
            width: 2.3rem;
            height: 0.4rem;
            margin-right: 0.4rem;
            border-radius: 0.04rem;
            border: 1px solid #D9DBE1;
            font-size: 0.13rem;

            .ant-input:placeholder-shown {
                font-size: 0.13rem;
            }
        }
    }

    .card-main {
        margin-top: 0.18rem;

        .ant-table-thead>tr>th {
            background: #F2F2FD;
            color: #9696A3;
            font-size: 0.13rem;
            line-height: 1;
            padding: .13rem 0 .13rem .3rem;

            &:nth-child(2) {
                padding-left: .24rem;
            }
        }

        .ant-table-tbody>tr>td {
            padding: 0 0 0 .3rem;
            height: 0.6rem;
            font-size: 0.14rem;

            &:nth-child(2) {
                padding-left: 0;
            }

        }

        .ant-table-cell::before {
            height: 0px !important;
        }

        .pay-link {
            width: 0.42rem;
            height: 0.14rem;
            font-size: 0.14rem;
            font-weight: 400;
            line-height: 0.14rem;
            color: #5C5EE4;
            opacity: 1;
        }

        // table一级展开项
        .ant-table-row-level-1 {
            background: #EFEFFB;

            td {
                padding: 0px;
                height: 0.4rem !important;
            }
        }

        .children-text {
            color: #9696A3;
            font-size: .13rem;
        }

        .product-name-text {
            font-size: .14rem;
            margin-left: 0.2rem;

            .host-name-text {
                font-size: .13rem;
                color: #9696A3;
            }
        }
    }


    .product_names {
        position: relative;
        height: 0.14rem;

        &:hover .product-type {
            opacity: 1;
        }

        .product-type {
            display: inline;
            padding: 0 0.03rem;
            height: 0.20rem;
            position: absolute;
            top: 0;
            left: 0;
            transform: translateY(calc(-100% - .06rem)) translateX(calc(-100% + .16rem));
            background: #FFFFFF;
            box-shadow: 0px 0.06rem 0.12rem rgba(30, 52, 90, 0.1200);
            opacity: 0;
            transition: all .5s;
            // position: fixed;
            z-index: 1000;
        }

        .product-text {
            height: 0.2rem;
            font-size: 0.14rem;
            font-family: Source Han Sans CN;
            font-weight: 400;
            line-height: 0.2rem;
            color: #040E34;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;

            img {
                width: 0.16rem;
                margin-right: 0.08rem;
            }
        }
    }

    .gateway {
        position: relative;
        height: 0.14rem;

        &:hover .gateway-num {
            opacity: 1;
        }

        .gateway-num {
            display: inline;
            padding: 0 0.03rem;
            height: 0.20rem;
            position: absolute;
            top: 0;
            left: 0;
            transform: translateY(calc(-100% - 0.04rem)) translateX(-25%);
            background: #FFFFFF;
            box-shadow: 0px 0.06rem 0.12rem rgba(30, 52, 90, 0.1200);
            opacity: 0;
            transition: all .5s;
        }

        .gateway-text {
            height: 0.14rem;
            font-size: 0.14rem;
            font-family: Source Han Sans CN;
            font-weight: 400;
            line-height: 0.14rem;
            color: #5C5EE4;
            cursor: pointer;

        }
    }

    .gateway-pay {
        color: #5C5EE4;
    }

    .ant-table-expanded-row-fixed {
        padding-left: 0px !important;
        padding-right: 0px !important;
    }

    .expand-icon {
        margin-right: 0.04rem;

        svg {
            width: 80%;
        }
    }

    // 支付弹窗样式 
    .pay-modal {
        .ant-modal-header {
            border-radius: .12rem .12rem 0 0;
            line-height: 1;
            padding: .4rem .3rem .3rem .3rem;
            border-bottom: 0;
            font-size: .18rem;
            color: #040E34;
        }

        .ant-modal-content {
            width: 6.4rem !important;
            border-radius: .12rem;
        }

        .ant-modal-body {
            padding-top: 0;
            padding-bottom: .4rem;
        }

        .ant-modal-footer {
            text-align: center;
            border-top: 0;
            padding: .16rem 0 .38rem;
        }

        .ant-btn {
            width: .88rem;
            line-height: .4rem;
            height: .4rem;
            border-radius: .04rem;
            padding: 0;
            font-size: .14rem;

            span {
                line-height: 1;
            }

            &.sure {
                background: #5C5EE4;
                color: #fff;
            }

            &.cancel {
                &:hover {
                    background: #5C5EE4;
                    color: #fff;
                }
            }
        }

        .cloud-pay-box {
            .item {
                display: flex;
                justify-content: flex-start;

                &.first,
                .ant-select-selector,
                .ant-select-selection-item {
                    line-height: .44rem;
                    height: .44rem;
                    font-size: .14rem;
                    border-radius: .04rem;
                }

                .ant-select-selection-item {
                    color: #9696A3;
                }

                &.first {
                    margin-bottom: .52rem;
                }

                &.last {
                    margin-top: .32rem;
                }
            }

            .ant-select {
                width: 5rem;
                display: block;
                height: .44rem;
                line-height: .44rem;
            }

            .l-text {
                width: .8rem;
                font-size: .13rem;
                color: #040E34;
            }

            .ant-checkbox-wrapper {
                margin-bottom: .24rem;
                line-height: 1;
            }

            .order-num {
                font-size: .14rem;
                color: #040E34;
                font-weight: 400;
            }

            .pay-num {
                font-size: .16rem;
                color: #FFA800;
                line-height: 1;

                p {
                    margin-bottom: 0;
                }

                .paid {
                    font-weight: 500;
                    margin-bottom: .1rem;
                }

                .balance {
                    font-size: .13rem;
                    margin-left: -.04rem;
                }
            }

            .pay-html {
                margin-left: 0;
                width: 100%;
                border: none;
                display: flex;
                justify-content: center;
            }
        }
    }

}
@primary-color: #5C5EE4;